//Gallery Films
.gallery {
  @include flexBox($w: wrap, $jc: start);
}

.movie-card {
  @include for-size(phone) {
    margin: 15px;
  }
  @include for-size(tablet) {
    width: calc((100% - 4 * 15px) / 2);
    margin: 15px;
  }
  @include for-size(desktop) {
    width: calc((100% - 6 * 15px) / 3);
    margin: 15px;
  }
}
.movie-image {
  border-radius: 5px;
  margin-bottom: 10px;

  @include for-size(phone) {
    width: 280px;
    height: 402px;
  }
  @include for-size(tablet) {
    width: 294px;
    height: 398px;
  }
  @include for-size(desktop) {
    width: 274px;
    height: 398px;
  }
}

.container {
  @include for-size(phone) {
    width: 280px;
  }
  @include for-size(tablet) {
    width: 618px;
  }
  @include for-size(desktop) {
    width: 882px;
  }
}
.movie-title {
  // width: 113px;
  height: 31px;

  font-style: normal;
  font-weight: 500;

  text-transform: uppercase;

  color: #000000;
  font-family: Roboto;
  font-size: 12px;

  line-height: 16px;
  letter-spacing: 0em;
  text-align: left;
}

.genre {
  color: $accent-text;
  margin-right: 3px;
}
.genre:not(:first-child) {
  margin-left: 3px;
}
.movie-release-date {
  color: $accent-text;
  margin-right: 3px;
}
.movie-release-date::before {
  content: '|';
}

.genre:first-child::after {
  content: ', ';
  color: #ff6b08;
}

.movie-vote {
  color: $white;
  background-color: $accent-icon;
  margin-left: 10px;
  padding: 2px 9px 1px;
  border-radius: 5px;
}

.genre-release-date {
  @include flexBox($w: nowrap, $jc: start);
  font-size: 12px;
}
